import { Component, OnInit, AfterViewInit } from '@angular/core';
import {trigger, state, style, animate, transition, stagger, query } from "@angular/animations"
import { AnalyticsService } from 'src/app/services/analytics/analytics.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss'],
  animations: [
    trigger('bannerTrigger', [
      transition(":enter", [
        query("*", [
          style({ opacity: 0, transform: "translateX(-50px)" }),
          stagger(50, [
            animate(
              "250ms cubic-bezier(0.35, 0, 0.25, 1)",
              style({ opacity: 1, transform: "none" })
            )
          ])
        ])
      ])
    ])
  ]
})
export class BannerComponent implements OnInit {  

  latitude: number;
  longitude: number;
  location: string;

  constructor(
    private http: HttpClient,
    public analyticsService: AnalyticsService
  ) { }

  ngOnInit(): void {
    this.http.get(`https://api.geoapify.com/v1/ipinfo?apiKey=${environment.geolocationAPIKey}`).subscribe(data => {
      this.latitude = data['location']['latitude'];
      this.longitude = data['location']['longitude'];
      let city = String(data['city']['name']).split(" ")[0];
      let state = String(data['state']['name']);
      let country = String(data['country']['name']);
      let flag = String(data['country']['flag']);
      this.location = flag + " "+ city +", "+ state +", "+ country;
    }); 
  }
  

}
